.productDetails {
  background-color: rgb(245, 245, 245);
  width: 100vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;
  display: flex;
}

.productDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
  border: 2px solid white;
  background-color: white;
}
.productDetails > div:last-child {
  align-items: flex-start;
}
.carouselBlock {
  text-align: center;
}

.carousel-slider {
  margin: 0 auto !important;
}
.animated {
  padding: 0 !important;
}
.detailsBlock-1 > h2 {
  color: rgb(54, 54, 54);
  font: 600 1.6vmax "Roboto";
  margin:1vmax 0;
}
.detailsBlock-1 > p {
  color: rgba(54, 54, 54, 0.582);
  font: 200 0.6vmax cursive;
}
.detailsBlock-2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 70%;
  padding: 1vmax 0;
}

.detailsBlock-2 > span {
  color: #ffa41c;
  font-size: 1vmax;
}
.detailsBlock-2-span {
  color: rgba(54, 54, 54, 0.582) !important;
  font: 200 0.8vmax cursive !important;
  margin-left: 5px;
}
.detailsBlock-3 {
  width: 70%;
}
.detailsBlock-3 > h1 {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.8vmax "Franklin Gothic Medium";
  margin: 1vmax 0;
}
.detailsBlock-3-1 {
  display: flex;
  align-items: center;
}
.detailsBlock-3-1-1 > button {
  border: none;
  outline: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.3vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
}
.detailsBlock-3-1-1 > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}
.detailsBlock-3-1-1 > input {
  border: none;
  outline: none;
  width: 1vmax;
  padding: 0.5vmax;
  text-align: center;
  font: 400 0.8vmax "Roboto";
  color: rgba(0, 0, 0, 0.74);
}
.detailsBlock-3-1 > button:last-child {
  border: none;
  outline: none;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  background-color: tomato;
  font: 500 0.7vmax Roboto;
  border-radius: 20px;
  padding: 0.6vmax 2vmax;
  margin: 1vmax;
}
.detailsBlock-3-1 > button:last-child:hover {
  background-color: rgb(214, 84, 61);
}

.detailsBlock-3 > p {
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  padding: 1vmax 0;
  color: rgba(0, 0, 0, 0.651);
  font: 400 1vmax "Roboto";
  margin: 1vmax 0;
}
.detailsBlock-4 {
  color: rgba(0, 0, 0, 0.897);
  font: 500 1.2vmax sans-serif;
}
.detailsBlock-4 > p {
  color: rgba(0, 0, 0, 0.534);
  font: 300 0.8vmax sans-serif;
}
.reviews {
  display: flex;
  overflow: auto;
}
.addJustify {
  justify-content: center;
  margin: 2vmax 0;
}
.removeJustify {
  justify-content: flex-start;
}
.submitDialog {
  display: flex;
  flex-direction: column;
}
.submitDialogTextArea {
  height: 8vmax;
  border: 1px solid rgba(0, 0, 0, 0.126);
  margin: 1vmax 0;
  padding: 1rem;
  outline: none;
  font: 300 1rem "Roboto";
}
.submitReview {
  border: none;
  outline: none;
  background-color: tomato;
  font: 500 0.7vmax "Roboto";
  border-radius: 20px;
  padding: 0.6vmax 2vmax;
  margin: 1vmax 0;
  color: white;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}
.submitReview:hover {
  background-color: rgb(197, 68, 45);
  transform: scale(1.1);
}

.reviewsHeading {
  color: #000000be;
  font: 500 1.4vmax "Roboto";
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.226);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}
.reviewCard {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.226);
  border: 1px solid rgba(56, 56, 56, 0.116);
  width: 24.77vmax;
  align-items: center;
  display: flex;
  flex-direction: column;
  flex: none;
  margin: 1vmax;
  padding: 3vmax;
}
.reviewCard > img {
  width: 6vmax;
}
.reviewCard > p {
  color: rgba(0, 0, 0, 0.836);
  font: 600 0.9vmax Roboto;
}
.reviewCard > span {
  color: #ffa41c;
  font-size: 1.2vmax;
  margin: 5px;
}
.reviewCardComment {
  color: rgba(0, 0, 0, 0.445) !important;
  font: 300 0.8vmax cursive !important;
}
.noReviews {
  font: 400 1.3vmax Gill Sans;
  text-align: center;
  color: rgba(0, 0, 0, 0.548);
  height: 5vmax;
}

.carousel .thumb {
  height: 80px;
}
.RecommandedProducts{
  border-top: 1px solid rgba(0, 0, 0, 0.089);
}
.RecommandedProducts>div{
  display:flex;
  overflow-x: auto;
  margin: 0 2vmax;
}
.RecommandedProducts>h2{
 font:400 2vmax "Roboto";
text-align: center;
margin: 3vmax 0;
color: rgba(0, 0, 0, 0.452);


}
@media screen and (max-width: 600px) {
  .reviewCardComment {
    font: 300 1.4vmax "Roboto" !important;
  }
  .reviewCard > p{
    font:600 1.5vmax "Roboto";
  }
  .productDetails {
    flex-direction: column;
    height: unset;
  }
  .productDetails > div:last-child {
    align-items: center;
  }
  .detailsBlock-1 > h2 {
    font-size: 2vmax;
    text-align: center;
    margin:1vmax 0;
  }
  .detailsBlock-1 > p {
    text-align: center;
    font-size: 1vmax;
    margin:1vmax 0;
  }
  .detailsBlock-2 {
    justify-content: center;
  }
  .detailsBlock-2 > span {
    font-size: 1.5vmax;
  }
  .detailsBlock-3 > h1 {
    font: 700 3vmax "Franklin Gothic Medium";
    text-align: center;
  }
  .detailsBlock-3-1 {
    flex-direction: column;
  }
  .detailsBlock-3-1-1 {
    padding: 2vmax 0;
  }
  .detailsBlock-3-1-1 > button {
    padding: 0.9vmax;
    width: 4vmax;
  }
  .detailsBlock-3-1-1 > input {
    padding: 1.5vmax;
    width: 3vmax;
    font: 400 1.8vmax "Roboto";
  }
  .detailsBlock-3-1 > button:last-child {
    font: 500 1.7vmax "Roboto";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }
  .detailsBlock-3 > p {
    padding: 2.5vmax 0;
    text-align: center;
    font: 400 2vmax "Roboto";
  }
  .detailsBlock-4 {
    font: 500 2.5vmax sans-serif;
  }
  .detailsBlock-4 > p {
    font: 300 1.8vmax sans-serif;
  }
  .submitReview {
    font: 500 1.7vmax "Roboto";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }
  .carousel .thumbs-wrapper {
    display: none;
  }
  .carousel .control-dots {
    display:none;
  }
  .carousel.carousel-slider .control-arrow{
    display:none;
  }
}
